___CSS_LOADER_EXPORT___.push([module.id, `*{
    box-sizing: border-box;
}
body{
    min-height: 100vh;
}
#headersvg{
    height: 50px;
}
#tempchoose{
    position: absolute;
    display: flex;
    background-color: lightblue;
    top: 0px;
    left: 0px;
    border: 1px solid black;
    font-size: 2rem;
}
#tempchoose > *{
    border: 1px solid black;
    padding: 2px;
    margin: 0px;
    flex: 1;
    text-align: center;
}
header{
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 30vh;
}
header > div {
    display: flex;
    align-items: center;
}
h1{
    font-size: 3rem;
}
input[type="text"]{
    width: 100%;
    border-radius: 4px;
    border: 1px solid #ccc;
    background-color: #f8f8f8;
    font-family: Arial, sans-serif;
    font-size: 16px;
    color: #333;
}
form{
    width: 40%;
    display: flex;
    gap: 8px;
}
.grid{
    font-family: Arial, Helvetica, sans-serif;
    border-radius: 8px;
    padding-left: 10px;
    padding-right: 10px;
    background-color: #dedede;
    width: clamp(400px,60vw,800px);
    font-size: 1.4rem;
    display: grid;
    grid-template-columns: repeat(5,1fr);
    grid-template-rows: repeat(5,auto);
    grid-template-areas:
    "Condition Condition ResolvedAddress ResolvedAddress ResolvedAddress"
    "weathersymbol weathersymbol Temperature Temperature Temperature"
    "weathersymbol weathersymbol PrecProb PrecProb PrecProb"
    "weathersymbol weathersymbol Description Description Description"
    "AlertHead Alert Alert Alert Alert";
}
#condition{
    grid-area: Condition;
    text-align: center;
}
#resolvedaddress{
    grid-area: ResolvedAddress;
}
#weathersymbol{
    grid-area: weathersymbol;
    width: 60%;
    justify-self: center;
    height: auto;
}
#temperature{
    grid-area: Temperature;
}
#precprob{
    grid-area: PrecProb;
}
#description{
    grid-area: Description;
}
#alerthead{
    grid-area: AlertHead;
    margin-right: 16px;
}
#alert{
    grid-area: Alert;
    align-self: center;
}
main{
    justify-items: center;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;AAC1B;AACA;IACI,iBAAiB;AACrB;AACA;IACI,YAAY;AAChB;AACA;IACI,kBAAkB;IAClB,aAAa;IACb,2BAA2B;IAC3B,QAAQ;IACR,SAAS;IACT,uBAAuB;IACvB,eAAe;AACnB;AACA;IACI,uBAAuB;IACvB,YAAY;IACZ,WAAW;IACX,OAAO;IACP,kBAAkB;AACtB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,gBAAgB;AACpB;AACA;IACI,aAAa;IACb,mBAAmB;AACvB;AACA;IACI,eAAe;AACnB;AACA;IACI,WAAW;IACX,kBAAkB;IAClB,sBAAsB;IACtB,yBAAyB;IACzB,8BAA8B;IAC9B,eAAe;IACf,WAAW;AACf;AACA;IACI,UAAU;IACV,aAAa;IACb,QAAQ;AACZ;AACA;IACI,yCAAyC;IACzC,kBAAkB;IAClB,kBAAkB;IAClB,mBAAmB;IACnB,yBAAyB;IACzB,8BAA8B;IAC9B,iBAAiB;IACjB,aAAa;IACb,oCAAoC;IACpC,kCAAkC;IAClC;;;;;uCAKmC;AACvC;AACA;IACI,oBAAoB;IACpB,kBAAkB;AACtB;AACA;IACI,0BAA0B;AAC9B;AACA;IACI,wBAAwB;IACxB,UAAU;IACV,oBAAoB;IACpB,YAAY;AAChB;AACA;IACI,sBAAsB;AAC1B;AACA;IACI,mBAAmB;AACvB;AACA;IACI,sBAAsB;AAC1B;AACA;IACI,oBAAoB;IACpB,kBAAkB;AACtB;AACA;IACI,gBAAgB;IAChB,kBAAkB;AACtB;AACA;IACI,qBAAqB;AACzB","sourcesContent":["*{\r\n    box-sizing: border-box;\r\n}\r\nbody{\r\n    min-height: 100vh;\r\n}\r\n#headersvg{\r\n    height: 50px;\r\n}\r\n#tempchoose{\r\n    position: absolute;\r\n    display: flex;\r\n    background-color: lightblue;\r\n    top: 0px;\r\n    left: 0px;\r\n    border: 1px solid black;\r\n    font-size: 2rem;\r\n}\r\n#tempchoose > *{\r\n    border: 1px solid black;\r\n    padding: 2px;\r\n    margin: 0px;\r\n    flex: 1;\r\n    text-align: center;\r\n}\r\nheader{\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    min-height: 30vh;\r\n}\r\nheader > div {\r\n    display: flex;\r\n    align-items: center;\r\n}\r\nh1{\r\n    font-size: 3rem;\r\n}\r\ninput[type=\"text\"]{\r\n    width: 100%;\r\n    border-radius: 4px;\r\n    border: 1px solid #ccc;\r\n    background-color: #f8f8f8;\r\n    font-family: Arial, sans-serif;\r\n    font-size: 16px;\r\n    color: #333;\r\n}\r\nform{\r\n    width: 40%;\r\n    display: flex;\r\n    gap: 8px;\r\n}\r\n.grid{